export function Post(props)
{
    const [postInit, setPostInit] = useState([])
   const [post,setPost] =useState([])
    const [postSet,postDidSet]=useState(false)
    useEffect(()=>{
        const final = [...props.newPost].concat(postInit)
        // console.log(post)
      if (final.length !== post.length) {
          // console.log(final)
        setPost(final)
      }
    }, [props.newPost, post, postInit])

  useEffect(() =>{
      if (postSet===false){
      const myCallback =(response,status) =>{
          if (status===200){
              setPostInit(response.data);
              postDidSet(true)
          }else {
              alert( "there was an error")
          }
 }
getMethod(myCallback)}
},[postInit,postDidSet,postSet])
return(
       post.map((post,index) => {
        return   <Post_description_tag  post ={post} className='col' key={index}/>
        }))
}

