export function ActionBtn(props) {
    const {post, action} = props

    const [likes,setlikes]=useState(post.likes ?post.likes: 0)
    const [userlikes,setuserliked]=useState(post.is_like)
    const className = props.className ? props.className : 'btn btn-primary btn-sm'
    console.log(userlikes)

  const forLikesFunction=(event)=>{
  event.preventDefault()
  if(userlikes){
console.log("like req")
                forLike(
                {
    id: post.id,
    action:"like" },
    (response,status)=>{
    console.log(response,response.DoLikes)
    if(status==200){

    if(response.DoLikes){
    setuserliked(false)
    setlikes(response.likes)
    console.log(likes)
    }else{console.log("like")
    setuserliked(false)}
    }})

             }
             else {console.log("unlike req")
                forLike(
                {
    id: post.id,
    action:"unlike" },
    (response,status)=>{
    if(status==200)
    {
    if(response.DoLikes){
    setuserliked(true)
    setlikes(response.likes)
    console.log(likes)}else{console.log("unlike")
    setuserliked(true)}
             }
             }
             )

             }


  }
    const handelClick = (event)=>{

        forLike({
    id: post.id,
    action:action.type
                },forLikesFunction)

    }
    return <button className={className} onClick={forLikesFunction}>{likes} {action.type}</button>
  }


