
function getCookie(name) {
    var cookieValue = null;
    if (document.cookie && document.cookie !== '') {
        var cookies = document.cookie.split(';');
        for (var i = 0; i < cookies.length; i++) {
            var cookie = cookies[i].trim();
            // Does this cookie string begin with the name we want?
            if (cookie.substring(0, name.length + 1) === (name + '=')) {
                cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
                break;
            }
        }
    }
    return cookieValue;
}


function lookup(method,endpoint,callback,data,bool=true){
    let jsonData;

    if(data){
        if (bool){
        jsonData= JSON.stringify(data)
        }
        else {
            jsonData= data
        }
    }


    const xml = new XMLHttpRequest()
    const csrftoken = getCookie('csrftoken');
    console.log(csrftoken)
    const url = `http://127.0.0.1:8000/api/${endpoint}`;
    xml.responseType="json"

    xml.open(method,url)
    if(bool){
    xml.setRequestHeader("Content-Type","application/json")
    }
    xml.setRequestHeader("Authorization", "Token 08f49cec6f948906b3c15882d53ab507567753bf")
    if (csrftoken){
        console.log("csrf token")
        xml.setRequestHeader("HTTP_X_REQUESTED_WITH","XMLHttpRequest")
        xml.setRequestHeader("X-Requested-With" ,   "XMLHttpRequest")
        xml.setRequestHeader("X-CSRFToken" ,csrftoken)
    }
    xml.onload = function()
    {
    callback(xml.response,xml.status)
        }
        xml.onerror = function (e){
        console.log(e)
            console.log(xml.response,xml.status)
        callback({'message':'this request have a error'},400)
        }
    xml.send(jsonData)
}
export  function postMethod(myFormData,bool,callback){
lookup('POST','post_upload',callback,myFormData,false)


}
